.card-limits-main{
  margin-top: 100px;
  text-align: center;
  margin-bottom: 200px;
}

.card-limits{

  box-shadow: 0 14px 14px rgba(0, 0, 0, 0.13);
  padding-bottom: 20px;

  .title{
    position: relative;

    font-family: $Museo_sans-700;

    color: $grey6;
    background-color: $grey13;

    span{
      margin-right: 20px;
      font-size: 17px;
      line-height: 57px;

      @include respond-to(tablet){
        margin-right: 15px;
        font-size: 15px;
        line-height: 50px;
      }
      @include respond-to(mobile){
        margin-right: 10px;
        font-size: 13px;
        line-height: 43px;
      }
    }

    a{
      position: absolute;
      top: 18px;
      @include respond-to(tablet){
        top: 12px;
      }
      @include respond-to(mobile){
        top: 9px;
      }
    }
  }

  .item{

    table{
      color: $grey8;
      font-family: $Museo_sans-300;
      font-size: 14px;
      line-height: 32px;
      text-align: left;

      th{
        padding-left: 30px;
        font-family: $Museo_sans-Medium;
        font-size: 16px;
        line-height: 56px;

        color: $grey14;

        @include respond-to(tablet){
          padding-left: 20px;
          font-size: 14px;
          line-height: 50px;
        }
        @include respond-to(mobile){
          padding-left: 10px;
          font-size: 12px;
          line-height: 44px;
        }
      }

      td{
        text-align: right;
      }
      td:first-child{
        text-align: left;
        padding-left: 45px;
        @include respond-to(tablet){
          padding-left: 35px;
        }
        @include respond-to(mobile){
          padding-left: 25px;
        }
      }
      td:nth-child(3n) {
        text-align: left;
        color: $grey12;
        font-size: 10px;
      }
      td:nth-child(4n) {
        color: $grey12;
        text-transform: uppercase;
      }
    }


  }
}