.products{
    width: 100%;
    text-align: left;
    background-color: $white;

    @include cf();

    table{
        width: 100%;
        tr {
            padding-left: 15px;
        }
    }

    .archive{
        display: block;
        padding-right: 45px;
        span {
            float: right;

            margin: 30px 0;

            line-height: 16px;
            font-family: $Museo_sans-300;
            font-size: 16px;
            text-decoration: underline;
            color: $blueColor;
            &:hover{
                text-decoration: none;
            }
        }
    }


    &__item{
        border-bottom: 2px solid $grey11;

        .icon{
            width: 10%;
            padding-top: 25px;
            padding-bottom: 25px;
            padding-left: 20px;
            padding-right: 0;
            img{
                display: block;
            }

        }

        .number{
            padding-left: 10px;

            line-height: 30px;
            color: $grey8;
            font-family: $Museo_sans-300;
            font-size: 16px;
        }
        .name{
            padding-left: 10px;

            line-height: 30px;
            font-family: $Museo_sans-Medium;
            font-size: 17px;
            color: $grey9;
        }
        .money{
            text-align: right;

            line-height: 30px;
            font-family: $Museo_sans-Medium;
            font-size: 17px;
            color: $grey9;
            text-transform: uppercase;
            &_currency {
                color: $grey12;
            }
            span{
                padding: 5px;
            }
        }

    }

    //ACCOUNTS
    &_accounts{

        td {
            padding-top: 5px;
            padding-bottom: 25px;
            text-align: left;
            vertical-align: bottom;
        }
        .cards-title {
            border-bottom: none;
            .cards-title__td{
                padding-top: 25px;
                padding-bottom: 5px;
                .pen{
                    margin-top: -5px;
                    margin-left: 13px;
                }
            }
            input {
                margin-left: 20px;
                width: 40%;

                line-height: 15px;
                font-family: $Museo_sans-Medium;
                font-size: 14px;
                color: $blueColor;
            }
        }

        .transfer {
            width: 30%;
            padding-left: 10px;

            i{
                margin-top: 7px;
                @include inline-blocks();
            }
            a{
                text-decoration: none;
            }

        }

        .money{
            padding-bottom: 25px;
            padding-right: 25px;
            position: relative;
            .icons_settings{
                top: -10px;
            }
        }
    }

    //CARDS
    &_cards{
        .money{
            padding-right: 25px;
            position: relative;
        }
    }

    //CREDITS and DEPOSITS
    &_credits{

        .action{
            width: 20%;

            line-height: 30px;
            font-family: $RobotoRegular;
            font-size: 13px;

            a{
                text-decoration: none;
                color: $grey9;
                display: block;
            }

            .icons{
                display: inline-block;
                content: "";
                @include bg-svg($cards-blue);
            }
        }
    }

    //TEMPLATES
    &_templates {
        .table-name {
            padding-top: 20px;
            padding-left: 37px;
            line-height: 20px;

            color: $grey8;
            font-family: $Museo_sans-300;
            font-size: 13px;
            line-height: 18px;
            @include respond-to(mobile) {
                padding-left: 15px;
            }
        }

        .products__item {
            .name {
                width: 40%;
                padding-left: 37px;
                line-height: 25px;

                color: $grey8;
                font-family: $Museo_sans-300;
                font-size: 11px;
            }
            .money {
                //padding-right: 20px;
                width: 35%;
                font-size: 13px;
            }
            .repeat {
                line-height: 50px;

                a {
                    color: $grey9;
                    font-family: $RobotoRegular;
                    font-size: 13px;
                    text-decoration: none;
                }

                .icons {
                    margin-right: 11px;

                    vertical-align: middle;
                    display: inline-block;
                    content: "";
                    @include bg-svg($exchange-blue-sm);
                }
            }

        }
    }
}

.overview_accounts--mob,
.overview_products--mob,
.overview_templates--mob {
    .col-xs-12,
    .col-xxs {
        display: none;

        @include respond-to(mobile) {
            display: block;
            @include blocks-justify();
            margin: 0 auto;
            td {
                padding-bottom: 0;
            }

            .icon {
                padding-top: 0;
            }

            .cards-title {
                input {
                    margin-left: auto;
                    width: auto;
                }
            }

            .transfer {
                width: auto;
                padding-left: 0;
            }

            .icons_settings {
                right: -20px;
            }
        }
    }
    .col-md-12 {
        padding: 0;
        @include respond-to(mobile) {
            display: none;
        }
    }
}

.overview_accounts--mob {
    .col-xs-12,
    .col-xxs {
        .products__item {
            display: table-row;
            vertical-align: middle;
            margin: 0 auto;

            border-bottom: 2px solid $transparent;

            .money {
                text-align: left;
                .icons_settings {
                    right: 50px;
                }
            }
            td {
                padding-left: 10px;
            }
        }
    }

}
.overview_products--mob {
    .col-xs-12,
    .col-xxs {
        .products__item {
            display: table-row;
            vertical-align: middle;
            margin: 0 auto;

            border-bottom: 2px solid $transparent;

            .name {
                padding-top: 20px;
                padding-left: 10px;
            }
        }
        .cards-title {
            &:not(:first-child) {
                border-top: 2px solid $grey11;
            }
        }
    }

}

.overview_templates--mob {
    .col-xs-12,
    .col-xxs {
        .products__item {
            display: table-row;
            vertical-align: middle;
            margin: 0 auto;

            border-bottom: 2px solid $transparent;

            .name {
                display: table;
                padding-left: 15px;
                width: auto;
                margin: 0 15px;
            }
            .money {
                text-align: left;
                padding-left: 15px;
            }
            .repeat {
                padding-left: 15px;
            }
        }
        .cards-title {
            &:not(:first-child) {
                border-top: 2px solid $grey11;
            }
        }
    }

}


//
.stock {
    position: relative;
}
.stock::before {
    position: absolute;
    left: 32px;
    top: -20px;
    content: "";
    border-bottom: 10px solid $green;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    height: 0;
    width: 55px;
}
.stockt {
    position: relative;
}
.stockt::before {
    position: absolute;
    left: 32px;
    left: 32px;
    top: -20px;
    content: "!";

    text-align: center;
    color: $grey10;
    font-family: $RobotoRegular;
    font-size: 16px;
    font-weight: 500;

    border-top: 10px solid $green;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    height: 0;
    width: 55px;
}
