.header {
    background-color: $blueColor;
    padding-top: 34px;
    padding-bottom: 22px;
    @include respond-to(tablet) {
        padding: 25px 0;
    }
}
.container {
    //width: 1170px;
    margin: 0 auto;

    //padding-left: 0;
    //padding-right: 0;

    position: relative;
    //@include blocks-justify();

    //@include respond-to(tablet) {
    //    width: 640px;
    //}
    //@include respond-to(mobile) {
    //    width: 320px;
    //}
}

.logo {

    position: absolute;
    left: 0;

    font-size: 0;
    @include respond-to(tablet) {
        position: relative;

        text-align: center;
    }
    @include respond-to(mobile) {
        position: relative;

        text-align: center;
    }
}

.main-nav {
    padding-left: 225px;
    @include respond-to(tablet) {
        padding-left: 0;
        margin: 0 auto;
    }
    @include respond-to(mobile) {
        padding-left: 0;
        margin: 0 auto;
    }
    &__wrap {
        @include blocks-justify();
    }

    &__list {
        @include inline-blocks();
        padding: 0;
        @include respond-to(tablet) {
            display: block;
            text-align: center;
        }
        @include respond-to(mobile) {
            display: none;
        }
        li {
            @include inline-blocks();
            margin: 0 15px;
            a {
                color: $white;
                font-family: $Museo_sans-700;
                font-size: 14px;

                line-height: 36px;
                font-weight: 700;
                //transform: scaleX(1);
                text-transform: uppercase;
                text-decoration: none;
                &:hover {
                    //transform: scaleX(1.1);
                    //transition: all 0.2s ease-out;
                }
            }
        }
    }
}

.actions {
    @include inline-blocks();
    @include blocks-justify();

    //font-size: 0;
    @include respond-to(tablet) {
        display: block;

        text-align: center;
    }
    @include respond-to(mobile) {
        display: block;

        margin-top: 20px;
        text-align: center;
    }

    &__set {
        margin-top: 10px;
        @include inline-blocks();
        @include blocks-justify();
        li {
            @include inline-blocks();
            margin: 0 12px;
            @include respond-to(mobile) {
                margin: 0 5px;
            }
        }
    }
    .langs {
        margin: 0 10px;

        @include inline-blocks();

        //font-size: 0;

        @include respond-to(tablet) {
            margin: 0 15px;
        }

        @include respond-to(mobile) {

            margin: 0 5px;
        }

        .chzn-container {
            width: 54px!important;
        }
        .chzn-container-single .chzn-single {
            background: none;
            padding: 0;
            height: 37px;
            border: none;
            border-radius: 0;

            box-shadow: none;
            color: $transparent;
            text-decoration: none;
            white-space: nowrap;
            line-height: inherit;
            span {
                width: 53px;
                height: 37px;

                padding-left: 10px;

                border-radius: 1px;
                border: 1px solid #f3f3f3;
                background-color: #1471d1;

                color: $white;
                font-family: $Museo_sans-Medium;
                font-size: 14px;
                line-height: 35px;
                font-weight: 500;
                text-transform: uppercase;

                box-sizing: border-box;

                cursor: pointer;
            }
            div {
                width: 25px;
                @include respond-to(tablet) {
                    left: 0;
                    right: auto;
                }
                @include respond-to(mobile) {
                    left: 0;
                    right: auto;
                }

                b {
                    margin-top: 8px;

                    //background: none;
                    @include bg-svg($chevron_d-wh);
                }
            }
        }
    }
}


/* HEADER-LOG for login page */

.header__log {
    @include blocks-justify();
}
.login {
    @include inline-blocks();

    @include respond-to(mobile) {
        display: block;

        margin: 0 auto;
    }

    span {
        color: $white;
        font-family: $Museo_sans-700;
        font-size: 15px;
        line-height: 34px;
        font-weight: 700;
        text-decoration: none;
        text-transform: uppercase;
        @include respond-to(mobile) {
            display: block;
            text-align: center;
        }
    }
}