.content-sec {

    padding-top: 20px;

    .content {
        padding-left: 225px;
        @include respond-to(tablet) {
            padding-left: 0;
        }
        @include respond-to(mobile) {
            padding-left: 0;

            .col1-m {
                width: calc(100% / 1 );

                display: block;

                box-sizing: border-box;
            }
        }
    }
}

/* currency */

.overview_currency,
.overview_converter {
    margin: 0 12px;
    margin-bottom: 6px;

    .overview__inner {
        //@include blocks-justify();

        //padding-left: 26px;
        @include respond-to(mobile) {
            padding-left: 20px;
        }
    }
}

.currency__bid {
    @include inline-blocks();

    color: $grey;
    font-family: $Museo_sans-300;
    font-size: 13px;
    line-height: 35px;
    font-weight: 300;
}

.currency__list {
    @include inline-blocks();
    font-size: 0;
    @include blocks-justify();

    .currency__item {

        @include inline-blocks();
        //margin: 0 24px;
        //
        //@include respond-to(tablet) {
        //    margin: 0 12px;
        //}
        //@include respond-to(mobile) {
        //    margin: 0 5px;
        //}
        span {
            color: $grey;
            font-size: 13px;
            line-height: 35px;
            font-weight: 500;

            opacity: 0.53;
            &:not(:last-child) {
                margin-right: 5px;
            }
        }
    }
}


.overview {
    margin: 15px 0;

    background: $white;

    box-shadow: 0 14px 14px rgba(0, 0, 0, 0.13);

    text-align: left;

    box-sizing: border-box;

    &__title {
        display: block;

        background-color: $grey4;

        padding-left: 26px;

        .icons {
            margin-top: 12px;

        }
        .title__name {
            margin-left: 30px;
            color: $black;
            font-family: $Museo_sans-300;
            font-size: 13px;
            line-height: 49px;
            font-weight: 300;
            text-transform: uppercase;
        }
    }
    &__title_det {
        background: $transparent;

        padding: 0;

        color: $grey5;
        font-size: 11px;
        line-height: 18px;
        text-transform: capitalize;
        text-align: center;
    }

    &__data {
        text-align: center;
        color: $grey3;
        font-family: $Museo_sans-Medium;
        font-size: 21px;
        line-height: 53px;
        font-weight: 500;
    }
    .expand {
        &::before {
            content: "";
            @include bg-svg($chevron_d);
        }
    }
    &__wrap_det {
        color: $blueColor;
        font-size: 14px;
        line-height: 18px;
        text-align: center;
    }

    &____val {
        margin-right: 5px;

        text-transform: uppercase;
    }

    .overview-prod__list {
        margin-bottom: 19px;
        padding-bottom: 19px;
        @include blocks-justify();
    }
    .overview-prod__item {
        @include inline-blocks();
        padding: 5px;

        box-sizing: border-box;
    }

    .services {
        margin-bottom: 2px;

        font-size: 0;
        background-color: rgba($white,.9);

        box-shadow: 0 10px 23px 5px rgba($black, 0.05);
        border-radius: 2px;

        @include blocks-justify();

        &__name {
            padding-left: 13px;

            @include inline-blocks();

            text-align: left;

            font-size: 0;
            box-sizing: border-box;
            cursor: pointer;

            span {
                color: $grey6;
                font-family: $Museo_sans-300;
                font-size: 17px;
                line-height: 44px;
                font-weight: normal;
            }
        }
        .btn-wrap {
            text-align: left;
            margin-right: 18px;
        }
    }
}

/*  services  */

.cabinet-products {
    .sub-services {

        margin-top: 2px;
        padding-left: 40px;
        padding-right: 18px;

        box-shadow: 0 10px 11px 3px rgba($black, 0.09);
        background-color: $white;

        &__item {
            position: relative;
            padding-top: 25px;
            &:last-child {
                padding-bottom: 25px;
            }
        }
        &__input {

            @include inline-blocks();
            color: $blueColor;
            font-family: $Museo_sans-Medium;
            font-size: 14px;
            line-height: 24px;
            font-weight: 500;
        }

        &__acts {
            position: relative;

            padding-right: 25px;

            @include blocks-justify();
        }
        .acts__link {
            color: $grey8;
            font-family: $Museo_sans-300;
            font-size: 16px;
            line-height: 24px;
            font-weight: 300;
            text-decoration: none;
        }
        .overview__wrap_acts {
            @include inline-blocks();
            line-height: 24px;
            .overview__currency {
                color: $grey8;
                font-family: $Museo_sans-300;
                font-size: 16px;
                line-height: 24px;
                font-weight: 300;
            }
        }
        .icons {
            margin-top: 3px;
        }
        .archive {
            display: block;

            color: $blueColor;
            font-family: $Museo_sans-300;
            font-size: 16px;
            font-weight: 300;
            text-decoration: underline;
            text-align: right;
            &:hover {
                text-decoration: none;
            }
        }
    }

    .sub-services {
        display: none;
    }
    .products {
        display: none;
    }


    /* TRANSFERS */
    .client-input {
        input {
            width: 100%;
            margin-top: 0;
            margin-right: 0;
            padding-left: 14px;
            background-color: $grey4;

            color: $grey6;
            font-family: $Museo_sans-700 ;
            font-size: 13px;
            font-weight: bold;
            line-height: 24px;
            border-radius: 2px;
            border: 1px solid $grey11;

            box-sizing: border-box;
        }
        label {
            display: none;
        }
    }

    .overview_transfers {

        .cards-num {
            margin-bottom: 15px;
            width: 45%;
            @include respond-to(mobile) {
                width: auto;
            }
        }
        .cards-amount{
            margin-bottom: 15px;
            width: 30%;
            @include respond-to(mobile) {
                width: 50%;
            }
        }
        .purpose {
            width: 100%;
        }
        .btn-press{
            display: block;

            margin: 15px auto;

            font-size: 11px;
            line-height: 25px;
        }
    }



    /* PAYMENTS */


    .overview_payments {

        .sub-services {
            @include blocks-justify();
            @include respond-to(mobile) {
                padding: 0 20px;
            }
            &__list {
                padding-top: 15px;
                @include blocks-justify();


                li {
                    min-height: 39px;
                    @include inline-blocks();

                    &:not(:last-child){
                        border-right: 1px solid $grey14;
                    }

                    @include respond-to(mobile) {
                        @include inline-blocks();
                    }
                    a {
                        text-align: center;
                        display: block;

                        font-size: 13px;
                        line-height: 18px;
                        font-family: $Museo_sans-300;
                        text-decoration: none;
                        font-weight: 300;
                        color: $grey14;

                        @include respond-to(mobile) {
                            padding: 5px 10px;
                        }
                        span {
                            @include respond-to(mobile) {
                                display: none;
                            }
                        }
                        .icons {
                            display: none;
                            @include respond-to(mobile) {
                                @include inline-blocks();
                                opacity: 0.53;
                            }
                        }
                        .active {
                            opacity: 1;
                        }
                    }

                    .current {
                        font-size: 13px;
                        line-height: 18px;
                        color: $blueColor;
                        opacity: 1;
                    }
                }

            }
            &__descr .active {
                min-height: 110px;
            }
            &__descr {
                display: none;
                form {
                    padding: 10px 0;

                    //@include blocks-justify();
                    //@include inline-blocks();

                    box-sizing: border-box;

                    @include respond-to(mobile) {
                        margin-top: 15px;
                        padding: 0;

                        width: auto;
                        display: block;
                    }

                    .client-input {
                        margin-bottom: 10px;
                        @include blocks-justify();
                        width: 100%;

                        .phone {
                            width: 100%;
                            margin-top: 0;
                            margin-right: 0;
                            background-color: $grey4;
                        }

                        .amount {
                            width: 30%;

                            @include inline-blocks();
                            input {
                                padding-left: 7px;

                                font-size: 11px;
                                line-height: 24px;
                            }
                        }
                    }

                    .btn-press {
                        @include inline-blocks();
                        font-size: 11px;
                        line-height: 25px;
                    }
                }
                .partners {
                    //padding: 10px 20px;

                    //@include inline-blocks();
                    //@include blocks-justify();

                    box-sizing: border-box;
                    @include respond-to(mobile) {
                        padding: 0;
                    }
                    &__item {
                        @include inline-blocks();
                    }
                    &__box {
                        margin: 5px;
                        span {
                            display: block;

                            text-align: center;

                            margin-top: 5px;

                            color: $grey14;
                            font-family: $Museo_sans-300;
                            font-size: 10px;
                            line-height: 14px;
                            font-weight: 300;
                        }
                    }
                }
            }
        }

        .active {
            display: block;
        }
    }
}

.acts__inner {
    //@include inline-blocks();

    //margin-bottom: 15px;
    position: relative;

    font-size: 14px;

    z-index: 0;

    &::after {
        content: '';
        @include bg-svg($chevron_d);
        display: block;
        position: absolute;
        z-index: 0;
        top:0;
        right:0;
        cursor: pointer;
        @include respond-to(mobile) {
            top: 18px;
        }
    }
    span {
        color: $grey8;
        font-family: $Museo_sans-Medium;
        font-size: 14px;
        line-height: 18px;
        font-weight: 300;
        margin-right: 30px;
        position: relative;
        @include respond-to(mobile) {
            display: block;
        }
    }


    select {

        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        padding-right: 24px;
        cursor: pointer;
        background-color: transparent;
        border: 0;
        outline: 0;
        color: $grey8;
        font-family: $Museo_sans-Medium;
        font-size: 14px;
        line-height: 24px;
        font-weight: 300;
        position: relative;
        z-index: 1;

        option {
            color: $grey8;
            font-family: $Museo_sans-300;
            font-size: 16px;
            line-height: 24px;
            font-weight: 300;
            text-decoration: none;
        }
    }
    select:focus {
        border: 0;
        outline: 0;
    }
}

.acts__inner_blue {
    //margin-bottom: 0;
    &::after{
        //content: '';
        @include bg-svg($chevron_up-blue);
        top: 50%;
        right: 5px;
        margin-top: -12px;
        //top: 5px;
        transform: rotate(180deg);
    }
    //select {
    //    width: 100%;
    //    padding-right: 0;
    //}
}


/* Converter */

.overview_converter {
    .overview__inner {
        padding-top: 10px;
        @include respond-to(tablet) {
            padding: 0 10px;
            padding-top: 10px;
        }
    }
    .currency__bid {
        display: none;
    }
    .currency__bid_convert {
        @include inline-blocks();
        @include respond-to(mobile) {
            display: block;
            text-align: center;
        }
    }
    .form {
        &__input {
            @include inline-blocks();

            margin: 0 20px;
            color: $grey;
            font-size: 13px;
            line-height: 35px;
            font-weight: 500;

            @include respond-to(tablet) {
                margin: 0;
            }
            @include respond-to(mobile) {
                display: block;

                max-width: 115px;

                margin: 10px auto;
            }

            select {
                width: 50%;

                display: block;

                margin: 0 auto;
                padding-right: 0;
                //&::after {
                //    right: 0;
                //    top: 2px;
                //}
            }
            span {
                display: block;
            }
        }

        .acts__inner {
            &::after {
                top: 0;
                right: 25%;
            }
            span {
                margin-right: 0;

                text-align: center;
                font-size: 12px;
            }
        }
    }

    .client-input {
        @include inline-blocks();
        @include blocks-justify();
        @include respond-to(mobile) {
            display: block;
            margin: 0 auto;

            text-align: center;
        }
        &_converter {
            label {
                display: block;

                text-align: center;

                font-size: 12px;
            }

            input [value] {
                color: $grey;
            }
        }
    }
    .icons {
        margin-top: 10px;
        @include respond-to(mobile) {
            margin: 10px 0;

            transform: rotate(-90deg);
        }
    }
    .icons_arrow-r {
        @include respond-to(mobile) {
            display: none;
        }
    }
}


