//
.operations-table-desktop{
    width: 100%;
    @include respond-to(desktop){
        display: table;
    }
    @include respond-to(tablet){
        display: none;
    }
    @include respond-to(mobile){
        display: none;
    }

    border-radius: 5px;
    box-shadow: 0 14px 14px rgba(0, 0, 0, 0.13);

    background: $white;

    th:not(:first-child),
    td:not(:first-child){
        text-align: left;
        padding: 0 10px;
    }

    th:first-child{
        padding-left: 20px;
    }

    td:first-child{
        padding-left: 20px;
    }

    .filters {
        border-radius: 5px;
        border-bottom: 2px solid $grey11;
        line-height: 36px;

        font-size: 13px;
        color: $grey8;
        font-family: $Museo_sans-300;
        select{
            option{
                font-size: 13px;
                color: $grey8;
                font-family: $Museo_sans-300;
            }
        }
    }

    .categories {
        border-bottom: 2px solid $grey11;
        line-height: 36px;

        font-size: 13px;
        color: $grey8;
        font-family: $Museo_sans-700;

        .date{
            width: 16%;
        }

        .money{
            width: 14%;
        }

        .actions-cat {
            width: 15%;
            text-align: center;
        }
    }

    .operation{
        color: $grey8;
        font-family: $Museo_sans-300;
        font-size: 13px;
        line-height: 15px;

        .date{
            line-height: 46px;
            font-size: 11px;
            span:first-child{
                margin-right: 20px;
            }
        }

        .money{
            color: $grey9;
            text-transform: uppercase;

            .red{
                color: #ff2222;
            }
            .green{
                color: #31b102;
            }

            span{
                margin-right: 5px;
            }

            &_currency{
                color: $grey12;
            }
        }
        .actions-cat{
            text-align: center;

            margin: 12px 2px;
        }
    }
    .icons {
        margin: 0 5px;
    }
}

//
.operations-table-tablet{
    width: 100%;
    @include respond-to(desktop){
        display: none;
    }
    @include respond-to(tablet){
        display: block;
    }
    @include respond-to(mobile){
        display: none;
    }

    box-shadow: 0 14px 14px rgba(0, 0, 0, 0.13);
    color: $grey8;
    font-family: $Museo_sans-300;
    font-size: 17px;
    line-height: 25px;

    li{
        border-bottom: 2px solid $grey11;
    }

    .money:first-child{
        margin-right: 10px;
    }

    .money{
        color: $grey9;
        text-transform: uppercase;
        font-family: $Museo_sans-Medium;
        font-size: 19px;

        .red{
            color: #ff2222;
        }
        .green{
            color: #31b102;
        }

        &_currency{
            color: $grey12;
        }
    }

    .name {
        color: $grey9;
        font-family: $Museo_sans-Medium;
    }

    .col-sm-7{
        padding: 10px 20px;
        div:first-child{
            margin-bottom: 5px;
        }
    }
    .col-sm-3{
        padding: 10px 15px;
        text-align: right;
        span{
            display: block;
        }
    }
    .col-sm-2{
        padding: 10px 15px;
        height: auto;
        text-align: center;
        a {
            display: block;
        }
    }
}

//
.operations-table-mobile{
    width: 100%;
    @include respond-to(desktop){
        display: none;
    }
    @include respond-to(tablet){
        display: none;
    }
    @include respond-to(mobile){
        display: block;
    }

    box-shadow: 0 14px 14px rgba(0, 0, 0, 0.13);
    color: $grey8;
    font-family: $Museo_sans-300;
    font-size: 12px;
    line-height: 16px;

    li{
        border-bottom: 2px solid $grey11;
    }

    .money:first-child{
        margin-right: 7px;
    }

    .money{
        color: $grey9;
        text-transform: uppercase;
        font-family: $Museo_sans-Medium;
        font-size: 14px;

        .red{
            color: #ff2222;
        }
        .green{
            color: #31b102;
        }

        &_currency{
            color: $grey12;
        }
    }

    .name {
        color: $grey9;
        font-family: $Museo_sans-Medium;
    }

    .col-xs-8{
        padding: 10px 10px;
        div:first-child{
            margin-bottom: 5px;
        }
    }
    .col-xs-4{
        padding: 10px 10px;
        text-align: right;
        span{
            display: block;
        }
    }
}

.tabs{
    display: none;
}
.operations-table .active-table{
    display: block;
}
