.news-list{
}

.news-title{
    margin-top: 50px;

    text-align: center;
    line-height: 20px;

    font-family: $Museo_sans-700;
    font-size: 15px;
    text-transform: uppercase;
    color: $grey6;
}

.news-items{
    width: 100%;
    margin-top: 40px;
}

.news-item{
    width: 100%;

    .preview-image{
        img{
            width: 100%;
        }
    }

    .preview-info:not(:last-child) {
        margin-bottom: 40px;
    }

    .preview-info{
        padding-left: 40px;
        text-align: left;
        line-height: 24px;

        color: $grey6;
        font-size: 16px;
        font-family: $Museo_sans-Medium;

        .preview-name{
            display: block;
        }
        .preview-date{
            display: block;
            font-size: 12px;
        }
        .preview-text{
            display: block;
            color: $grey8;
            font-family: $Museo_sans-300;
        }
        .preview-more{
            //padding-right: 20px;
            display: block;

            @include blocks-justify();


            img:not(:last-child) {
                margin-right: 10px;
            }

            .more__link,
            .icons__wrap {
                @include inline-blocks();
            }
            .icons__wrap {
                .icons {
                    margin: 3px;
                }
            }
            .more__link {
                .icons {
                    margin-top: 5px;
                    margin-right: 10px;
                }
                span {
                    color: $grey6;
                    font-family: $Museo_sans-Medium;
                    font-size: 16px;
                    line-height: 20px;
                }
            }

            a {
                text-decoration: underline;
                &:hover {
                    text-decoration: none;
                }
            }
        }
    }
}