.cabinet-auth {

    .login-sec,
    .restore-sec {
        .container {
            margin: 42px auto;
        }

        .overview_login,
        .overview_otp,
        .overview_restore {
            .services__name {
                display: block;
                padding-left: 0;

                text-align: center;
            }

            .sub-services {
                .client-input__data {
                    .password,
                    .otp {
                        padding-left: 14px;
                    }
                }
            }
        }

        &__form {
            //@include inline-blocks();
            float: right;
            @include respond-to(tablet) {
                display: block;

                width: 55%;

                margin: 0 auto;
            }



            @include respond-to(mobile) {
                display: block;

                width: 85%;

                margin: 0 auto;
            }
        }
        .services-inner {
            margin-top: 0;
            @include respond-to(tablet) {
                margin-top: 30px;
            }
            @include respond-to(mobile) {
                margin-top: 25px;
            }

        }
        .sub-services {
            display: block;

            padding: 22px 49px 11px 49px;

            background-color: rgba($white,.9);
            @include respond-to(mobile) {
                padding: 20px 30px;
            }

        }
        .client-input__data:not(:first-child) {
            margin-top: 18px;
        }

        .client-input {
            label {
                display: block;
                text-align: left;

                color: $grey6;
                font-family: $Museo_sans-Medium;
                font-size: 14px;
                line-height: 24px;
                font-weight: 500;
            }
            input {
                display: block;

                width: 100%;
                padding-left: 14px;
                margin-top: auto;
                background: $white;

                color: $grey3;
                font-family: $Museo_sans-700;
                font-size: 13px;
                line-height: 32px;
                font-weight: bold;

                border: 1px solid $grey11;

                box-sizing: border-box;
                @include respond-to(mobile) {
                    width: 100%;
                    font-size: 15px;
                }
            }
        }

        .restore {
            margin-top: 10px;
            a {
                display: block;
                text-align: center;

                color: $blueColor;
                font-family: $Museo_sans-300;
                font-size: 10px;
                line-height: 16px;
                font-weight: 300;
                text-decoration: none;
                &:hover {
                    text-decoration: underline;
                }
            }
        }

        .checkbox {
            font-size: 15px;
            line-height: 15px;

            .check {
                @include inline-blocks();

                width: 16px;
                height: 15px;

                border: 1px solid $grey11;
            }
            label {
                @include inline-blocks();
            }
        }
        .btn-press {
            display: block;

            margin: 23px auto;
        }
    }

    .images-wrap {
        @include inline-blocks();
        font-size: 0;
        @include respond-to(tablet) {
            display: block;
        }
        img {
            box-shadow: 0 10px 11px 3px rgba($black, 0.09);
            @include respond-to(tablet) {
                display: block;
                margin: 0 auto;
            }
            @include respond-to(mobile) {
                width: 100%;
                display: block;
                margin: 0 auto;
            }
        }

    }

    .phone {
        input {
            width: 90%;

            padding-left: 28px;

            @include respond-to(mobile) {
                padding-left: 10px;
                width: 100%;
            }

            /* Webkit */
            &::-webkit-input-placeholder {
                letter-spacing: 3.2px;
                @include respond-to(mobile) {
                    letter-spacing: 0;
                }
            }

            /* Firefox 4-18 */
            &:-moz-placeholder {
                letter-spacing: 3.2px;
                @include respond-to(mobile) {
                    letter-spacing: 0;
                }
            }

            /* Firefox 19+ */
            &::-moz-placeholder {
                letter-spacing: 3.2px;
                @include respond-to(mobile) {
                    letter-spacing: 0;
                }
            }

            /* IE10+ */
            &:-ms-input-placeholder {
                letter-spacing: 3.2px;
                @include respond-to(mobile) {
                    letter-spacing: 0;
                }
            }

        }
    }



    .load-apps {
        &__title {
            display: block;

            color: $blueColor;
            font-family: $Museo_sans-300;
            font-size: 10px;
            line-height: 16px;
            font-weight: 300;

        }
        span {
            display: block;
            text-align: center;
        }
        .icons {
            margin: 0 8px;
        }
    }
}


.login-sec {
    min-height: 100%;
}