.profile-content{
  box-shadow: 0 14px 14px rgba(0, 0, 0, 0.13);
  margin-bottom: 50px;

  .photo{
    position: relative;
    div{
      position: absolute;
      bottom: 10px;
      text-align: center;
      margin: 0 auto;
      left: 0;
      right: 0;
      a{
        color: $white;
      }
    }
  }

  .title {
    @include respond-to(desktop){
      padding-left: 50px;
    }
    @include respond-to(tablet){
      padding-left: 30px;
    }
    @include respond-to(mobile){
      padding-left: 0;
      text-align: center;
    }
    background-color: $grey11;
    color: $grey6;
    font-family: $Museo_sans-700;
    font-size: 17px;
    line-height: 54px;
  }

  .info{
    position: initial;
    margin-bottom: 20px;

    @include respond-to(desktop){
      padding-left: 50px;
    }
    @include respond-to(tablet){
      padding-left: 30px;
    }
    @include respond-to(mobile){
      padding-left: 0;
    }
    ul{
      position: initial;
      margin-top: 20px;
      li{
        color: $grey8;
        font-family: $Museo_sans-300;
        font-size: 16px;
        line-height: 40px;
        span{
          line-height: 24px;
        }
        @include respond-to(tablet){
          line-height: 35px;
          span{
            line-height: 20px;
          }
        }
      }
      li span:first-child{
        color: $grey14;
        font-family: $Museo_sans-Medium;
        font-size: 14px;
      }
    }
  }

  .private{
    position: relative;
    img {
      width: 100%;
    }

    ul{
      li:last-child {
        position: absolute;
        bottom: 10px;
      }
    }
    &_mobile {
      margin-top: 20px;
      text-align: center;

      .info {
        position: initial;
        ul {
          li {
            line-height: 24px;
            margin-bottom: 10px;
          }

          li:last-child {
            margin: 20px 0;
            position: inherit;
          }
        }
      }
    }
  }

  .contact{
    .actions{
      margin: 5px 0;
      text-align: left;

      font-family: $Museo_sans-300;
      font-size: 12px;
      line-height: inherit;
      color: $grey8;

      select{
        padding: 3px 20px;
        color: $blueColor;
        background-color: $grey13;
      }

      input{
        padding: 3px 20px;
        font-weight: 500;
        background-color: $grey13;
      }
      img{
        margin-right: 10px;
      }
      .icons_plus-alt {
        vertical-align: middle;
      }
    }

    ul{
      li{
        div{
          line-height: initial;
        }
      }
    }
    &_mobile {
      margin-top: 20px;
      text-align: center;

      .actions{
        text-align: center;
      }
      .info {
        position: initial;
        ul {
          li {
            line-height: 24px;
            margin-bottom: 10px;
          }
          .actions{
            margin: 10px 0;
            line-height: inherit;
          }
        }
      }
    }
  }

  .settings{
    color: $grey9;
    font-family: $Museo_sans-300;
    font-size: 14px;
    line-height: 24px;
  }
  .red{
    color: #ff0000;
  }
  .blue{
    color: $blueColor;
  }
  .desktop-tablet{
    @include respond-to(desktop){
      display: block;
    }
    @include respond-to(tablet){
      display: block;
    }
    @include respond-to(mobile){
      display: none;
    }
  }
  .mobile{
    @include respond-to(desktop){
      display: none;
    }
    @include respond-to(tablet){
      display: none;
    }
    @include respond-to(mobile){
      display: block;
    }
  }
}