.sidebar {
    width: 230px;

    position: absolute;
    left: 0;

    font-size: 0;

    .bottom__client {
        margin-bottom: 15px;

        @include inline-blocks();

        font-size: 0;

        .client__img {
            @include inline-blocks();
            img {
                width: 34px;
                height: 32px;
            }
        }
        .client__name {
            width: 125px;
            margin-left: 13px;

            @include inline-blocks();
            color: $black;
            font-size: 12px;
            line-height: 14px;
            font-weight: 500;
        }
    }

    &__list {
        li {
            padding: 0 10px;
            &.active,
            &:hover {
                background-image: linear-gradient(to top, #eceff1 0%, #ffffff 100%);
                a {
                    color: $blueColor;
                }
            }
        }
        a {
            text-decoration: none;
            display: block;

            color: $grey3;
            font-family: $Museo_sans-900;
            font-size: 13px;
            line-height: 49px;
            font-weight: 900;
            text-transform: uppercase;

        }
    }

    .item__link_myprod::before {
        content: "";
        display: inline-block;
        vertical-align: middle;

        margin-right: 12px;

        @include bg-svg($myprod);
    }
    .item__link_branch::before {
        content: "";
        display: inline-block;
        vertical-align: middle;
        margin-right: 12px;

        @include bg-svg($branch);
    }
    .item__link_template::before {
        content: "";
        display: inline-block;
        vertical-align: middle;
        margin-right: 12px;
        @include bg-svg($template);
    }
    .item__link_atm::before {
        content: "";
        display: inline-block;
        vertical-align: middle;
        margin-right: 12px;
        @include bg-svg($atm);
    }
    .item__link_operations::before {
        content: "";
        display: inline-block;
        vertical-align: middle;
        margin-right: 12px;
        @include bg-svg($operations);
    }
    .item__link_exchange::before {
        content: "";
        display: inline-block;
        vertical-align: middle;
        margin-right: 12px;
        @include bg-svg($exchange);
    }

}

