// Buttons default styles

.btn-wrap {

    margin-top: 5px;

    background: $grey2;

    @include inline-blocks();

    font-size: 0;
    box-sizing: border-box;
    box-shadow: 0 1px 3px rgba($black, 0.15);
    border-radius: 1px;
    .icons_plus {
        @include bg-svg($plus);
        margin-top: 10px;
        margin-left: 10px;
    }

    .btn-act {
        padding-right: 8px;
        padding-left: 5px;

        background-color: $grey2;

        color: $grey7;
        font-family: $RobotoRegular;
        font-size: 11px;
        line-height: 34px;
        font-weight: 400;
        box-sizing: border-box;
    }


}
.btn-wrap_more {
    display: block;
    width: 24%;

    margin: 40px auto;
    padding: 0 17px;
    .icons_plus {
        margin-top: 10px;
        margin-left: auto;
    }
    .btn-act_more {
        line-height: 33px;
        padding-right: 0;
    }
}

.btn-search {
    @include inline-blocks();
}
.btn-press {
    position: relative;

    padding: 0 34px;

    background: $green;

    color: $green3;
    font-family: $GilroyExtraBold;
    font-size: 16px;
    line-height: 38px;
    font-weight: 400;
    text-transform: uppercase;
    border-radius: 5px;

    box-shadow: 0 6px $green4;

    transition: none;

    cursor: pointer;
    outline: none;
    &:hover {
        box-shadow: 0 4px $green4;
        top: 2px;
    }
    &::after {
        content: '';
        position: absolute;

        z-index: -1;
        transition: all 0.3s;
    }
}



