.cabinet-lead-form{
  [class*="col-"] {
    float: none;
  }
}
.slider{
  .title{
    padding-left: 50px;

    background-color: $grey4;

    font-size: 17px;
    line-height: 54px;
    color: $grey6;
    font-family: $Museo_sans-Medium;
    font-weight: bold;

    @include respond-to(tablet){

    }
    @include respond-to(mobile){

    }
  }
  .main-list{
    margin: 30px 0;
  }
  &-arrow {
    text-align: center;
    a{
      display: inherit;
    }
  }

  ul {
    text-align: center;
    li {
      position: relative;
      padding: 10px;
      img{
        width: 100%;
      }

      .info{
        position: absolute;

        padding: 0 5px;
        padding-bottom: 5px;

        margin: 0 auto;
        top: 30px;
        left: 0;
        right: 0;

        background-color: $white;

        span{
          display: block;

          font-size: 12px;
          line-height: 24px;
          color: $grey3;
          font-family: $Museo_sans-300;
        }
        span:not(:first-child){
          text-align: left;
          font-size: 11px;
          line-height: 16px;
          opacity: 0.8;
        }
      }
      .btn-press{
        margin: 0 auto;
        padding: 0 14px;

        font-size: 10px;
        line-height: 24px;

      }
      select{
        margin: 0 auto;

        font-size: 10px;
        line-height: 12px;
      }
    }
  }

  .desktop-tablet{
    @include respond-to(mobile){
      display: none;
    }
  }
}
