.map-head{
  text-align: left;

  @include respond-to(mobile){
    text-align: center;
    div {
      text-align: left;
      span {
          display: block;
          margin-left: 5px;
      }
    }
  }

  span{
    color: $grey7;
    font-family: $Museo_sans-300;
    font-size: 14px;
    line-height: 76px;
    @include respond-to(mobile){
      line-height: 20px;
    }
  }

  select{
    padding: 7px 0;
    margin: 22px 10px;

    @include respond-to(mobile){
      margin: 10px 0px;
      max-width: 400px;
    }

    border: 1px solid $grey11;
    border-radius: 2px;
    background-color: $grey4;
    opacity: 0.52;

    color: $grey7;
    font-family: $Museo_sans-300;
    font-size: 14px;
    line-height: 32px;
  }

  &__title{
    span{
      color: $grey6;
      font-family: $Museo_sans-700;
      font-size: 17px;
      line-height: 34px;
    }
  }

  &__search{
    position: relative;
    padding-right: 35px;
    text-align: left;

    border: 1px solid $grey11;

    box-sizing: border-box;

    @include respond-to(mobile){
      margin: 10px 0;
      max-width: 400px;
    }

    input{
      box-sizing: border-box;
      padding-left: 10px;
      width: 100%;

      color: $blueColor;
      font-family: $Museo_sans-700;
      font-size: 14px;
      line-height: 32px;
    }
    .btn-search {
      position: absolute;
      padding: 9px;
      top: 0;
      right: 0;
    }
  }

  &__list{
    margin: 22px 0;
    text-align: right;

    @include respond-to(mobile){
      margin: 10px 0;
      text-align: left;
    }

    li{
      text-align: center;
      display: inline-block;

      background-color: $grey4;
      border-radius: 2px;
      a{
        display: block;
        padding: 0 15px;

        color: $grey6;
        font-family: $Museo_sans-Medium;
        font-size: 14px;
        line-height: 32px;
        text-decoration: none;
      }
    }
    li.active {
      background-color: $grey12;
    }
    li:active {
      background-color: $grey12;
    }
  }
}

.content {
  .map-head__search{
    padding-right: 35px;
  }
}