.cabinet-registration{
  [class*="col-"] {
    float: none;
    padding: 0;
  }
}

.registration{
  text-align: center;
  display: inline-block;
  .registration{
    padding: 40px 0;
  }
}
.form-reg{
  text-align: right;
  @include respond-to(mobile){
    text-align: center;
  }
  .reg-input{
    line-height: 50px;
    @include respond-to(mobile){
      line-height: 20px;
    }

    label{
      padding-right: 15px;

      text-align: right;
      @include respond-to(mobile){
        text-align: left;
      }

      font-family: $Museo_sans-Medium;
      font-size: 12px;

      color: $blueColor;
    }

    label:last-child{
      padding-left: 0;
      text-align: left;
      font-family: $Museo_sans-300;
      font-size: 10px;
      line-height: 15px;
    }

    input{
      padding-left: 15px;
      margin: 15px 0;

      text-align: left;

      border: 1px solid $grey4;

      font-family: $Museo_sans-Medium;
      font-size: 13px;
      line-height: 32px;
      color: $grey3;
    }
    input:first-child{
      margin-right: 15px;
    }
  }
}


