// Mixins.

@mixin nl {
    margin: 0;
    padding: 0;
    text-indent: 0;
    list-style: none;
    list-style-position: outside;
}

@mixin cf {
    &:before,
    &:after {
        display: table;
        content: "";
    }

    &:after {
        clear: both;
    }

    .lt-ie8 & {
        zoom: 1;
    }
}

@mixin blocks-justify {
    text-align: justify;

    /*   */
    line-height: 0;
    font-size: 0; /* 1px  Opera */

    /*   IE6-7*/
    text-justify: newspaper;
    zoom:1;
    &:after {
        content: '';
        width: 100%;
        height: 0;
        visibility: hidden;
        overflow: hidden;
        @include inline-blocks();

    }
}


@mixin cf {
    &:before,
    &:after {
        display: table;
        content: "";
    }

    &:after {
        clear: both;
    }

    .lt-ie8 & {
        zoom: 1;
    }
}


@mixin inline-blocks {
    display: inline-block;
    vertical-align: top;
}

$break-small: 767px;
$break-large: 767px;
$break-large-right: 1199px;
$only-desk: 1200px;
$break-large-desktop:1600px;
@mixin respond-to($media) {
    @if $media == mobile {
        @media only screen and (max-width: $break-small) {
            @content;
        }
    } @else if $media == tablet {
        @media only screen and (min-width: $break-large) and (max-width: $break-large-right - 1) {
            @content;
        }
    } @else if $media == t-p {
        @media only screen and (min-width: $break-large) and (max-width: $break-large-right) and (orientation: portrait) {
            @content;
        }
    } @else if $media == t-and-m {
        @media only screen and (max-width: $break-large-right) {
            @content;
        }

    }@else if $media == d-l {
        @media only screen and (min-width: $break-large-desktop) {
            @content;
        }
    }@else if $media == desktop {
        @media only screen and (min-width: $only-desk) {
            @content;
        }

    }@else if $media == t-and-d {
        @media only screen and (min-width: $break-large) {
            @content;
        }
    }
}
