// Common styles of the project

body {
    height: 100%;
}

body {
    background: url("../img/content/main-bg.jpg") no-repeat 50% 50%;
    background-size: cover;

    font-family: $Museo_sans-Medium;
}

ol, ul {
    list-style: none;
}

b, strong {
    font-weight: bold;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video, option {
    margin: 0;
    padding: 0;
    border: 0;
}

a:active,
a:hover {
    text-decoration: none;
}

/* Reset `button` and button-style `input` default styles */
input,
button {
    background: none;
    border: 0;
    color: inherit;

    font: inherit;
    line-height: normal;
    overflow: visible;
    padding: 0;
    //-webkit-appearance: button; /* for input */
    -webkit-user-select: none; /* for button */
    -moz-user-select: none;
    -ms-user-select: none;

    &:focus {
        outline: 0;
    }
}

input::-moz-focus-inner,
button::-moz-focus-inner {
    border: 0;
    padding: 0;
}

.icons {
    @include inline-blocks();
}

.icons_setting-w {
    @include bg-svg($settings-w);
}

.icons_repeat {
    @include bg-svg($circling);
}

.icons_look {
    @include bg-svg($eye);
}
.icons_load {
    @include bg-svg($plane);
}
.icons_check {
    @include bg-svg($check-circle);
}
.icons_security {
    @include bg-svg($security);
}


.icons_chevron {
    @include bg-svg($chevron-d);
    margin-top: 10px;

    opacity: 0.53;
}
.opened .icons_chevron {
    @include bg-svg($chevron_up-blue);
    transform: rotate(0deg);
    transition: transform 2.8s linear;
}
.icons_write {
    @include bg-svg($pencil);
    font-size: 0;

    cursor: auto;
}
.icons_arrow-r {
    @include bg-svg($arrow-r);

    opacity: 0.53;
}
.icons_transfer {
    @include bg-svg($transfer);

    opacity: 0.53;
}
.icons_exit {
    @include bg-svg($exit);
}
.icons_mail {
    @include bg-svg($mail);
}
.icons_android {
    @include bg-svg($android);
}
.icons_apple {
    @include bg-svg($apple);
}
.icons_settings {
    position: absolute;
    top: 5px;
    right: 5px;

    @include bg-svg($settings);
}
.icons_smartphone {
    @include bg-svg($smartphone);
}
.icons_tv {
    @include bg-svg($tv);
}
.icons_communal {
    @include bg-svg($Communal);
}
.icons_all-service {
    @include bg-svg($all-service);
}
.icons_plus-alt {
    @include bg-svg($plus-alt);
}
.icons_search {
    @include bg-svg($search);
}
.icons_fb {
    @include bg-svg($fb);
}
.icons_tw {
    @include bg-svg($tw);
}
.icons_back-arrow {
    @include bg-svg($back-arrow);
}


form {
    input {
        /* Webkit */
        &::-webkit-input-placeholder { color: $grey; font-family: $Museo_sans-700;opacity: 0.52;}

        /* Firefox 4-18 */
        &:-moz-placeholder { color: $grey; font-family: $Museo_sans-700;opacity: 0.52;}

        /* Firefox 19+ */
        &::-moz-placeholder { color: $grey; font-family: $Museo_sans-700;opacity: 0.52;}

        /* IE10+ */
        &:-ms-input-placeholder { color: $grey; font-family: $Museo_sans-700;opacity: 0.52;}
    }

}
