.cabinet-templates {
  .overview {
    margin: 15px 0;
  }

  [class*="col-"] {
    float: left;
  }
}

.templates-title{
  li{
    text-align: center;
    display: inline-block;
    background-color: #edf0f2;
    span{
      display: block;
      line-height: 34px;

      color: $grey6;
      font-family: $Museo_sans-Medium;
      font-size: 17px;
      font-weight: bold;
      text-decoration: none;
    }
  }
  li:hover{
    background-color: #cdcdcd;
  }
}
.templates-products{

  text-align: center;

  .templates-squares{
    float: none;
    text-align: justify;

    margin-top: 10px;

    .templates-square{
      margin-bottom: 10px;
      padding: 15px;
      position: relative;
      @include inline-blocks();

      box-shadow: 0 3px 9px rgba(0, 0, 0, 0.13);

      text-align: center;

      a {
        color: $grey9;
        font-family: $Museo_sans-Medium;
        font-size: 13px;
        line-height: 20px;
      }

      span{
        margin-top: 10px;
        display: block;
      }

      .set{
        line-height: 13px;
        position: absolute;
        top:3px;
        right: 3px;
      }
      &_helper{
        width: 100%;
        height: 0;
        visibility: hidden;
      }
    }
    .templates-square:hover {
      transform: scale(1.1);
    }
  }

}

.templates-table {
  width: 100%;
  @include respond-to(desktop){
    display: table;
  }
  @include respond-to(tablet){
    display: table;
  }
  @include respond-to(mobile){
    display: none;
  }
  border-radius: 5px;
  box-shadow: 0 3px 9px rgba(0, 0, 0, 0.13);

  text-align: right;

  td{
    padding: 20px 0;
  }
  td:first-child{
    padding-left: 45px;
  }

  line-height: 20px;

  font-size: 14px;
  color: $grey9;
  font-family: $Museo_sans-300;

  .template-table-row{
    border-bottom: 2px solid $grey11;

    .name{
      text-align: left;
      line-height: 20px;
    }

    .money{
      position: relative;
      text-transform: uppercase;
      padding-right: 20px;

      .money_currency{
        margin-left: 10px;
        color: $grey12;
      }

      a{
        line-height: 13px;
        position: absolute;
        top:5px;
        right: 5px;
      }
    }


    .actions-cat{
      padding-top: 17px;
      a{
        margin-right: 20px;
      }
      a:last-child{
        margin-right: 40px;
      }
    }

  }
}
.templates-table-mobile{
  width: 100%;
  @include respond-to(desktop){
    display: none;
  }
  @include respond-to(tablet){
    display: none;
  }
  @include respond-to(mobile){
    display: block;
  }

  border-radius: 5px;
  box-shadow: 0 3px 9px rgba(0, 0, 0, 0.13);


  line-height: 20px;

  font-size: 14px;
  color: $grey9;
  font-family: $Museo_sans-300;

  li {
    border-bottom: 2px solid $grey11;
    .col-xs-11{
      margin: 0 15px;
    }
  }
  .actions-cat{
    line-height: 30px;
    float: none;
    text-align: center;
    a:not(:last-child){
      margin-right: 20px;
    }
  }
  .name{
    line-height: 20px;
  }

  .money {
    line-height: 30px;
    position: relative;
    text-transform: uppercase;
    .money_currency {
      color: $grey12;
    }
    a{
      line-height: 13px;
      position: absolute;
      right: 0;
    }
  }
}


//overview

.form-templates{
  font-size: 14px;
  line-height: 28px;

  @include respond-to(tablet){
    font-size: 11px;
    line-height: 22px;
  }
  @include respond-to(mobile){
    font-size: 9px;
    line-height: 18px;
  }

  form{
    text-align: center;
  }
  &__title{
    font-family: $Museo_sans-700;
    font-size: 18px;
    @include respond-to(tablet){
      font-size: 14px;
      line-height: 50px;
    }
    @include respond-to(mobile){
      font-size: 12px;
      line-height: 40px;
    }
    line-height: 60px;
    color: $grey3;

    span:last-child{
      padding-left: 40px;
    }
    @include respond-to(tablet){
      span:last-child{
        padding-left: 20px;
      }
    }
    @include respond-to(mobile){
      span:last-child{
        padding-left: 10px;
      }
    }
  }

  .client-input{
    padding: 20px 0;
    ::placeholder{
      color: $blueColor;
    }

    label{
      padding-right: 40px;

      font-family: $Museo_sans-300;
      font-weight: normal;
      @include respond-to(tablet){
        padding-left: 15px;
        padding-right: 20px;
        line-height: 28px;
      }
      @include respond-to(mobile){
        padding-left: 15px;
        padding-right: 10px;
        line-height: 20px;
      }
      text-align: right;
      line-height: 32px;
      color: $grey8;
    }
    input{
      text-align: left;
      line-height: 32px;
      padding-left: 40px;
      @include respond-to(tablet){
        //padding-left: 20px;
        line-height: 28px;
      }
      @include respond-to(mobile){
        //padding-left: 10px;
        line-height: 20px;
      }
      border: 1px solid $grey4;

      font-family: $Museo_sans-300;
      font-weight: normal;
      color: $blueColor;
    }
    span{
      font-family: $Museo_sans-300;
      font-size: 14px;
      @include respond-to(tablet){
        font-size: 11px;
      }
      @include respond-to(mobile){
        font-size: 9px;
      }
      color: $grey3;
    }
  }

  .btn-press{
    font-size: 11px;
    margin: 0 10px;
    margin-bottom: 45px;

    &_del{
      background: $grey12;
      box-shadow: 0 6px $grey8;
    }
  }
}

.back{
  display: block;
  margin-left: 15px;
  margin-bottom: 50px;

  line-height: 24px;
  a{
    margin-left: 10px;
    font-size: 16px;
    @include respond-to(tablet){
      font-size: 13px;
    }
    @include respond-to(mobile){
      font-size: 10px;
    }

    color: $grey6;
    font-family: $Museo_sans-300;
    font-weight: normal;
    text-decoration: underline;
  }
}