.footer {
    padding-top: 50px;
    padding-bottom: 30px;

    .main-nav {

        &_footer {
            @include blocks-justify();
            @include respond-to(tablet) {
                margin: 15px auto;
            }
            @include respond-to(mobile) {
                margin: 15px auto;
            }
            .contacts {
                @include inline-blocks();
                @include respond-to(tablet) {
                    display: block;
                }
                @include respond-to(mobile) {
                    display: block;

                    text-align: center;
                }
                span {
                    @include respond-to(tablet) {
                        display: block;
                    }
                    @include respond-to(mobile) {
                        display: block;
                    }
                }
                &__link {
                    color: $grey15;
                    font-size: 13px;
                    line-height: 16px;
                    text-transform: uppercase;
                }
                .link__phone {
                    font-family: $Museo_sans-Medium;
                    font-weight: 500;
                }
                .link__text {
                    margin-right: 5px;

                    font-family: $Museo_sans-Medium;
                    font-weight: 500;
                    @include respond-to(mobile) {
                        margin-right: 0;
                    }
                }
                .small {
                    font-size: 11px;
                    line-height: 14px;
                    font-family: $Museo_sans-300;
                    font-weight: 300;
                }
            }
            .main-nav__list {
                @include respond-to(tablet) {
                    margin-top: 15px;

                    display: block;

                    text-align: left;
                }
                @include respond-to(mobile) {
                    margin-top: 15px;

                    display: block;

                    text-align: center;
                }
                li {
                    margin-right: 20px;
                    margin-left: auto;

                    @include respond-to(mobile) {

                        display: block;

                        margin-right: 0;

                        text-align: center;
                    }
                    a {
                        display: block;

                        color: $black;
                        font-family: $Museo_sans-Medium;
                        font-size: 10px;
                        line-height: 14px;
                        font-weight: 500;
                        text-transform: uppercase;
                        &:hover {
                            opacity: .7;
                        }
                        @include respond-to(mobile) {

                            margin: 10px 0;
                        }
                    }
                }
            }
        }

    }
    .licence {
        position: absolute;
        left: 0;

        color: $black;
        font-size: 10px;
        line-height: 14px;
        text-transform: uppercase;
        text-align: left;

        @include respond-to(tablet) {
            position: relative;

            text-align: left;
        }
        @include respond-to(mobile) {
            position: relative;

            text-align: center;
        }

        span {
            display: block;
        }
        &__name {
            font-family: $Museo_sans-Medium;
            font-weight: 500;
        }
        &__num {
            font-family: $Museo_sans-Medium;
            font-weight: 500;
        }
    }


}